<template>
  <div id="notAuthMain">
    <p style="font-weight:bold;font-size:26px;">Not Authority</p>
    <p>对不起，您无权限访问网站，请先登录！<span>{{second}}</span>秒后 <a @click="login" style="color:#41babc" title="点击跳转到登录页面">跳转到登录页面</a></p>
    
  </div>

</template>

<script>
export default {
  mounted(){
    this.timer = setInterval(()=>{
      this.second--
      // console.log(this.second)
      if(this.second===0){
        clearInterval(this.timer)
        this.$router.push("/login")
      }
    },1000)
    
  },
  beforeDestory(){
    clearInterval(this.timer)
  },
  data(){
    return {
      second:20,
      timer:null
    }
  },
  methods:{
    login(){
      clearInterval(this.timer)
      this.$router.push("/login")
    }
  }
}
</script>

<style lang="less" scoped>
 #notAuthMain{
   color:#000;
   padding: 20px;
   h1{
     font-size: 32px;
     margin-bottom: 25px;
   }
   p{
     font-size: 16px;
     span{
       color:#41babc;
       padding:0 3px;
     }
     a{
       text-decoration: underline;
     }
   }
 }
</style>




